import { LGraphNode, LiteGraph } from "src/litegraph";
import { NodeParams } from "../Graph";

/**
 * 
 * @param title
 */
export class RawDefineNode extends LGraphNode {
  value_widget: any;
  properties: any;
  widgetType: RawDefineNodeTypeEnum;
  title: string;

  constructor(title: string = "", options: NodeParams) {
    super(title);
    this.properties = Object.assign(
      { precision: 0, value: 0, step: 10 },
      options
    );

    let widgetType: string;
    switch (options?.widgetType) {
      case RawDefineNodeTypeEnum.Boolean:
        widgetType = "toggle";
        this.properties.value = false;
        break;
      default:
        widgetType = "number";
        this.properties.value = 0;
    }

    this.value_widget = this.addWidget(
      widgetType,
      "",
      this.properties.value,
      (v) => {
        this.setProperty("value", v);
        this.setOutputData(0, v);
      },
      this.properties
    );

    // 
    // this.addInput("A", "number");
    this.addOutput("", "number");
  }

  //function to call when the node is executed
  onExecute = function () {
    // 
    this.setOutputData(0, this.properties.value);
  };
}

export enum RawDefineNodeTypeEnum {
  Boolean = "boolean",
  String = "string",
  Number = "number",
}

export class RawBooleanDefineNode extends RawDefineNode {
  constructor(title: string = "", options: NodeParams) {
    super(title, {
      ...options,
      widgetType: RawDefineNodeTypeEnum.Boolean,
    });
  }
}
export class RawStringDefineNode extends RawDefineNode {
  constructor(title: string = "", options: NodeParams) {
    super(title, {
      ...options,
      widgetType: RawDefineNodeTypeEnum.String,
    });
  }
}

//Constant
export class ConstantNumber extends LGraphNode {
  static title = "Const Number";
  static desc = "Const number";
  
  widget: any;
  widgets_up: boolean;
  size: [number, number];

  constructor() {
    super("Const Number");
    this.addOutput("value", "number");
    this.addProperty("value", 1.0);
    this.widget = this.addWidget("number", "value", 1, "value");
    this.widgets_up = true;
    this.size = [180, 30];
  }

  onExecute() {
    this.setOutputData(0, parseFloat(this.properties["value"]));
  }

  getTitle() {
    if (this.flags.collapsed) {
      return this.properties.value;
    }
    return this.title;
  }

  setValue(v: any) {
    this.setProperty("value", v);
  }

  onDrawBackground(ctx: any) {
    this.outputs[0].label = this.properties["value"].toFixed(3);
  }
}


export class ConstantBoolean extends LGraphNode {
  static title = "Const Boolean";
  static desc = "Const boolean";
  
  widget: any;
  serialize_widgets: boolean;
  widgets_up: boolean;
  size: [number, number];

  constructor() {
    super("Const Boolean");
    this.addOutput("bool", "boolean");
    this.addProperty("value", true);
    this.widget = this.addWidget("toggle", "value", true, "value");
    this.serialize_widgets = true;
    this.widgets_up = true;
    this.size = [140, 30];
  }

  getTitle() {
    if (this.flags.collapsed) {
      return this.properties.value;
    }
    return this.title;
  }

  onExecute() {
    this.setOutputData(0, this.properties["value"]);
  }

  setValue(v: any) {
    this.setProperty("value", v);
  }

  onGetInputs() {
    return [["toggle", LiteGraph.ACTION]];
  }

  onAction(action: any) {
    this.setValue(!this.properties.value);
  }
}


export class ConstantString extends LGraphNode {
  static title = "Const String";
  static desc = "Const string";
  
  widget: any;
  widgets_up: boolean;
  size: [number, number];

  constructor() {
    super("Const String");
    this.addOutput("string", "string");
    this.addProperty("value", "");
    this.widget = this.addWidget("text", "value", "", "value");
    this.widgets_up = true;
    this.size = [180, 30];
  }

  getTitle() {
    if (this.flags.collapsed) {
      return this.properties.value;
    }
    return this.title;
  }

  onExecute() {
    this.setOutputData(0, this.properties["value"]);
  }

  setValue(v: any) {
    this.setProperty("value", v);
  }

  onDropFile(file: any) {
    var that = this;
    var reader = new FileReader();
    reader.onload = function (e: any) {
      that.setProperty("value", e.target.result);
    };
    reader.readAsText(file);
  }
}

LiteGraph.registerNodeType("basic/string", ConstantString);
LiteGraph.registerNodeType("basic/boolean", ConstantBoolean);
LiteGraph.registerNodeType("basic/const", ConstantNumber);


// //register in the system
LiteGraph.registerNodeType("basic/raw", RawDefineNode, "");
LiteGraph.registerNodeType(
  "basic/raw_bool",
  RawBooleanDefineNode,
  ""
);